/* Import the colors */
@import './colours.css';

.about-section {
    position: relative; /* Ensure relative positioning for layering */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 4rem); /* Subtract top and bottom padding from 100vh */
    overflow: hidden; /* Prevent overflow issues */
    padding: 2rem;
}

.about-content {
    position: relative; /* Ensure content is above the particle background */
    z-index: 1; /* Higher z-index than .particle-bg */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    /* padding: 10rem; */
}

.about-info {
    flex: 1;
    max-width: 70%; /* Limit the width of the text */
    padding: 1rem;
}

.about-info h1 {
    /* font-size: 3rem; */
    margin-bottom: 1rem;
    color: var(--h1-colour); /* Using variable for text color */
    text-align: left;
}

.about-info p {
    /* font-size: 1.2rem; */
    line-height: 1.5;
}

