/* for box photo */
.about-photo-wrapper {
    flex: 0.5;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    /* height: 100%; */
    /* padding-top: 3rem;
    padding-left: 1.5rem; */
    margin: auto;
    border-top: 0px;
}

.about-photo {
    max-height: 80vh; /* Limit height to avoid oversizing */
    object-fit: contain; /* Maintain aspect ratio */
    border: none; /* No border or shape */
    border-radius: 120px;
}



.particle-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    z-index: 0; /* Lower z-index to stay behind content */
    pointer-events: none;
    opacity: 0.7;
}

.resume-download-btn,
.linkedin-btn {
    display: inline-flex;
    align-items: center;
    border: 1px solid var(--paragraph-colour);
    border-radius: 6px;
    padding: 0.5rem 1rem;
    margin-top: 12px;
    text-decoration: none;
    color: inherit;
    font-weight: 500;
    gap: 0.5rem;
    cursor: pointer;
}

.resume-download-btn:hover,
.linkedin-btn:hover {
    background-color: var(--highlight-colour1);
    color: black;
    border: 1px solid black;
    font-weight: 600;
}

.resume-download-btn {
    margin-right: 1rem;
}

.resume-icon {
    width: 18px !important;
    height: 18px !important;
    font-size: 18px !important;
    vertical-align: top;
}


@media (max-width: 650px) {
    .about-section {
        flex-direction: column; /* Stack items vertically */
        text-align: center;
        padding-top: 2rem;
        padding-bottom: 2rem;
        height: fit-content;
    }

    .about-content {
        flex-direction: column; /* Align content vertically */
        align-items: center;
    }

    .about-info {
        max-width: 100%; /* Full width for text */
        margin-bottom: 1rem; /* Add spacing between text and image */
        text-align: center; /* Center-align text */
    }

    .about-photo-wrapper {
        width: 100%; /* Full width for the photo wrapper */
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .about-photo {
        max-height: 50vh; /* Smaller photo for mobile */
        object-fit: contain; /* Maintain aspect ratio */
        /* width: 100%; */
        border-radius: 200px;
    }
}
